import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.css'

let theme = { 
    primary: '#90A4AE', 
    secondary: '#e57373', 
    accent: '#9c27b0', 
    error: '#f44336', 
    warning: '#ffeb3b', 
    info: '#2196f3', 
    success: '#4caf50'
}

Vue.config.productionTip = false
Vue.use(Vuetify)

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>'
})



