<template>
    <v-footer app dark style="font-size: 11px;">
        <v-spacer></v-spacer>
        {{ groupStatus }}
        <v-spacer></v-spacer>
        {{ portfolioStatus }}
        <v-spacer></v-spacer>
        test test test
        <v-spacer></v-spacer>
    </v-footer>
</template>
<script>
import utilsMixin from '@/mixins/utilsMixin'
export default {
    mixins: [utilsMixin],
    computed: {
        groupStatus() {
            let model = this.$store.state.model
            let numGroups = model.groups.length
            let numDefinitions = model.definitions.length
            let totalWeight = this.$store.getters['model/totalWeight']
            return `Model: ${model.metaDetails.name} Groups: ${numGroups} Definitions: ${numDefinitions} Weight: ${totalWeight.toFixed(2)}%`
        },
        portfolioStatus() {
            let numSecurities = this.$store.state.portfolio.securities.length
            let portfolioValue = this.$store.state.portfolio.securities
                .reduce((acc, cur) => acc + cur.market_value, 0)
            return `Securities: ${numSecurities} Portfolio: ${this.$options.filters.money(portfolioValue)}`
        }
    }
}
</script>



