export default {
    data: () => ({
        rules: {
            required: (v) => !!v || 'This field is required',
            number: (v) => !isNaN(+v) || 'This field must be a number',
            commaString: (v) => {
                let result = 'This field must be a well-formed comma delimitted string'
                if(v) {
                    let exec = v.match(/(\d\s?)+(\s*,\s*(\d\s?)+)*/i)
                    if(exec) result = exec[0].length == v.length ? true : result
                }
                return result
            },
            uniqueDefinitionIdentifiers: (v) => {
                let definitions = store.state.model.definitions
                let identiiers = definitions
                    .reduce((acc, cur) => acc.push(...cur.identiiers.split(',')), [])
                    .map(identifier => identifier.trim())
                    .map(identifier => identifier.toLowerCase())
                return identiiers.indexOf(v.toLowerCase().trim()) < 0 || 'Must not contain previous identifiers'
            }
        }
    }),
    filters: {
        shorten(v, length = 20) {
            if(v && v.length >= length) {
                v = v.slice(0, length - 3) + '...'
            }
            return v
        },
        decimal(v, n) {
            if(typeof v === 'string') {
                v = +v.replace(/[^\d.]/g, '')
                v = isNaN(v) ? 0 : parseFloat(v)
                return v.toFixed(n)
            }
            else if(typeof v === 'number') {
                return v.toFixed(n)
            }
        },
        money(v) {
            let parts = v.toString().split(".")
            parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ", ")
            return '$ ' + parts.join(".")
        },
        prefix(v, n) {
            return n + '' + v
        },
        postfix(v, n) {
            return v + '' + n
        },
        buyOrSell(v) {
            return v.indexOf('-') >= 0
                ? 'SELL ' + v.replace('-', '')
                : 'BUY ' + v
        }
    },
    methods: {
        saveFile(blob, saveAs) {
            const blobUrl = URL.createObjectURL(blob)
            const link = document.createElement('a')
            link.style = 'display: none'
            document.body.appendChild(link)
            link.href = blobUrl
            link.download = saveAs
            link.click()
            document.body.removeChild(link)
        },
        loadFile(inputAttributes, fileReaderHandlers, readAsEnum) {
            const input = document.createElement('input')
            Object.entries(inputAttributes).forEach(([k, v]) => {
                input.setAttribute(k, v)
            })
            input.onchange = () => {
                const reader = new FileReader()
                reader.onloadstart
                Object.entries(fileReaderHandlers).forEach(([k, v]) => {
                    reader[k] = v
                })
                if(input.files.length > 0) reader[readAsEnum](input.files[0])
            }
            input.click()
        }
    }
}

export const readAsEnum = Object.freeze({
    TEXT: 'readAsText',
    BINARY_STRING: 'readAsBinaryString'
})


