<template>
    <v-container fluid grid-list-md>
        <v-layout row wrap>
            <v-flex xs12>
                <v-card>
                    <v-card-title>
                        Rebalance Paramaters
                        <v-spacer></v-spacer>
                        <v-text-field ref="threshold"
                        autocomplete="off"
                        :rules="[rules.required, rules.number]"
                        v-model="threshold"
                        label="Threshold Target (%)"
                        placeholder="0.0">
                        </v-text-field>
                    </v-card-title>
                </v-card>
            </v-flex>
        </v-layout>
        <v-layout row wrap>
            <v-flex xs12 sm6 md4 lg3 xl2 v-for="(item, i) in accounts" :key="i">
                <account-summary :account="item"></account-summary>
            </v-flex>
        </v-layout>
        <v-layout row wrap justify-center>
            <v-flex xs12>
                <trades-table class="ma-0 pa-0" :threshold="threshold"></trades-table>
            </v-flex>
        </v-layout>
    </v-container>
</template>
<script>
import AccountSummary from '@/components/AccountSummary'
import TradesTable from '@/components/TradesTable'
import portfolioMixin from '@/mixins/portfolioMixin'
import utilsMixin from '@/mixins/utilsMixin'
export default {
    mixins: [portfolioMixin, utilsMixin],
    components: {
        'account-summary': AccountSummary,
        'trades-table': TradesTable
    },
    data: () => ({
        threshold: 0.0
    })
}
</script>

<style>
</style>



