<template>
    <v-container fluid grid-list-md>
        <v-data-iterator :items="items" content-tag="v-layout" row wrap>
            <v-flex slot="item" slot-scope="props" xs12 >
                <v-card>
                    <v-card-title><h4>{{props.item.security}}</h4></v-card-title>
                    <v-divider></v-divider>
                    <v-list dense>
                        <v-list-tile>
                            <v-list-tile-content>CUSIPs:</v-list-tile-content>
                            <v-list-tile-content class="align-end">{{ props.item.cusip | shorten(100)}}</v-list-tile-content>
                        </v-list-tile>
                        <v-list-tile>
                            <v-list-tile-content>Target Value ($):</v-list-tile-content>
                            <v-list-tile-content class="align-end">{{ props.item.targetValue | decimal(2) | money }}</v-list-tile-content>
                        </v-list-tile>
                        <v-list-tile>
                            <v-list-tile-content>Current Value ($):</v-list-tile-content>
                            <v-list-tile-content class="align-end">{{ props.item.currentValue | decimal(2) | money }}</v-list-tile-content>
                        </v-list-tile>
                        <v-list-tile>
                            <v-list-tile-content>Proposed Value ($):</v-list-tile-content>
                            <v-list-tile-content class="align-end">{{ props.item.proposedValue | decimal(2) | money }}</v-list-tile-content>
                        </v-list-tile>
                        <v-list-tile>
                            <v-list-tile-content>Difference Value ($):</v-list-tile-content>
                            <v-list-tile-content class="align-end">{{ props.item.differenceValue | decimal(2) | money | buyOrSell }}</v-list-tile-content>
                        </v-list-tile>
                        <v-list-tile>
                            <v-list-tile-content>Target Percent (%):</v-list-tile-content>
                            <v-list-tile-content class="align-end">{{ props.item.targetPercent | decimal(2) | postfix(' %') }}</v-list-tile-content>
                        </v-list-tile>
                        <v-list-tile>
                            <v-list-tile-content>Current Percent (%):</v-list-tile-content>
                            <v-list-tile-content class="align-end">{{ props.item.currentPercent | decimal(2) | postfix(' %') }}</v-list-tile-content>
                        </v-list-tile>
                        <v-list-tile>
                            <v-list-tile-content>Proposed Percent (%):</v-list-tile-content>
                            <v-list-tile-content class="align-end">{{ props.item.proposedPercent | decimal(2) | postfix(' %') }}</v-list-tile-content>
                        </v-list-tile>
                        <v-list-tile>
                            <v-list-tile-content>Difference Percent (%):</v-list-tile-content>
                            <v-list-tile-content class="align-end">{{ props.item.differencePercent | decimal(2) | postfix(' %') }}</v-list-tile-content>
                        </v-list-tile>
                    </v-list>
                </v-card>
            </v-flex>
        </v-data-iterator>
    </v-container>
</template>
<script>
import utilsMixin from '@/mixins/utilsMixin'
import { createPortfolioInfo, createTrades } from '@/utils/portfolioUtils'
import AddSecurity from '@/components/AddSecurity'
export default {
    mixins: [utilsMixin],
    components: {
        'add-security': AddSecurity
    },
    props:['threshold'],
    computed: {
        items() {
            let securities = this.$store.getters['portfolio/getSecurities']
            let definitions = this.$store.getters['model/getDefinitions']
            let threshold = !isNaN(+this.threshold) || '0.0'
            return createTrades(createPortfolioInfo(definitions, securities), parseFloat(this.threshold))
        },
    },
    methods: {
        tryParse(val) {
            
        }
    },

}
</script>
<style>
</style>


