function createPortfolioInfo(definitions, securities) {
    let matches = {}
    definitions.forEach(d => {
        matches[d.id] = {
            definition: d,
            expectedWeight: d.weight,
            matches: findMatches(d, securities) 
        }
    })
    const portfolioValue = getPortfolioValue(securities)
    const matched = Object.values(matches).reduce((acc, cur) => acc.concat(cur.matches), [])
    const unmatched = securities.filter(s => matched.indexOf(s) < 0)
    return { portfolioValue, matches, matched, unmatched }
}

function findMatches(definition, securities) {
    let cusips = definition.identifiers.toLowerCase()
        .split(',')
        .map(s => s.trim())
    return securities.filter(s => cusips.indexOf(s.cusip.toString().toLowerCase().trim()) >= 0)
}

function createTrades(matchesInfo, threshold) {
    let unmatchedTrades = matchesInfo.unmatched.map(s => new Trade(s.security, s.cusip, matchesInfo.portfolioValue, s.market_value, 0, threshold))
    let matchedTrades = Object.values(matchesInfo.matches)
        .map(m => {
            let market_value = m.matches.reduce((acc, cur) => acc + cur.market_value, 0)
            return new Trade(m.definition.name, m.definition.identifiers, matchesInfo.portfolioValue, market_value, m.expectedWeight, threshold)
        })
    return [...matchedTrades, ...unmatchedTrades]
}


function getPortfolioValue(securities) {
    return securities.reduce((acc, cur) => acc + cur.market_value, 0)
}


class Trade {
    constructor(security, cusip, portfolioValue, market_value, targetPercent, thresholdPercent = 0) {
        this._security = security
        this._cusip = cusip
        this._portfolioValue = portfolioValue
        this._marktet_value = market_value
        this._targetPercent = targetPercent
        this._thresholdPercent = thresholdPercent
    }
    get security() {
        return this._security
    }
    get cusip() {
        return this._cusip
    }
    get targetValue() {
        return this._portfolioValue * (this._targetPercent / 100)
    }
    get currentValue() {
        return this._marktet_value
    }
    get proposedValue() {
        return ((100 - this._thresholdPercent) / 100) * this.targetValue
    }
    get differenceValue() {
        return this.proposedValue - this.currentValue
    }
    get targetPercent() {
        return this._targetPercent
    }
    get currentPercent() {
        return (this._marktet_value / this._portfolioValue) * 100
    }
    get proposedPercent() {
        return (this.proposedValue / this._portfolioValue) * 100
    }
    get differencePercent() {
        return this.targetPercent - this.currentPercent
    }
}

export { Trade, createPortfolioInfo, findMatches, createTrades }


